@use '../_utilities/_mixins/media-query' as *;
/* styles for generic template test*/
#george-block {
  max-width: 1650px;
  margin: 0 auto;
  #hero {
    #hero-image {
      grid-column-start: 1;
      grid-column-end: 8;
      background-image: url('https://images.unsplash.com/photo-1653496905315-fd11ea3ce50b?crop=entropy&cs=tinysrgb&fm=jpg&ixlib=rb-1.2.1&q=80&raw_url=true&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1740');
      background-position: center;
      background-size: cover;
      height: 85vh;
      min-height: 500px;
      @include mq(tablet) {
        grid-column-start: 1;
        grid-column-end: 15;
        grid-row: 1;
        &:after{
          position: absolute;
          content: "";
          width: 100%;
          height: 75%;
          bottom: 0;
          background: linear-gradient(0deg, rgba(0,0,0,0.8) 20%, rgba(0,0,0,0) 100%);
          mix-blend-mode: multiply;
        }
      }
    }
    .hero-text {
      grid-column-start: 8;
      grid-column-end: 15;
      h1 {
        text-align: left;
        font-family: var(--heading-font);
        font-weight: 700;
        text-transform: uppercase;
        max-width: 20ch;

      }
      p {
        font-weight: 300;
      }
      @include mq(tablet) {
        grid-column-start: 3;
        grid-column-end: 13;
        grid-row: 1;
        h1 {
          text-stroke: 1px var(--base-black);
          -webkit-text-stroke: 1px var(--base-black);
        }
      }
      @include mq(phablet) {
        grid-column-start: 2;
        grid-column-end: 14;
      }

    }

  }

  #introduction {

  }

  .breaker {
    .grid {
      img {
      }
    }
  }
}
